<script>
import {
    Modal,
    SearchForm,
    CardTitle,
    Timeline ,
    TestFeed ,
    NoContents
} from '../../components'

export default {
    name: 'LectureDetail',

    components : {
        'modal' : Modal,
        'search-form' : SearchForm,
        'timeline' : Timeline,
        'test-feed' : TestFeed,
        'no-contents' : NoContents
    },

    data () {
        return {
            modal: {
                aadd:false
            },
          msg: '     - TEST',
          lec_idx:null,
          lecture:{},
          companies: [],
          groups:[],
          students: [],
          tempStudents:[] ,  //   
          kpi:[],
          sessionTab:-1,
          sessionTab2:-1, //    
          groupTab:'' , //  
          chooseTeam:{}, //   
          companyTab:0,
          tab:0, //   
          attendanceCount : 0, //  
          avgAttendancePercent : 50 // 
        }
    },// data



    // Filter
    filters: {
      capitalize: function (value) {
        if (!value) return ''
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },




    created(){
        var id = this.$ro.history.current.params.id
        this.$set(this, 'lec_idx', id)
        // console.log(this.lec_idx);
        this.getLecture(id)

        // $On
        this.$EventBus.$on('addTeam', this.addTeam)
    },

    methods: {

        chooseGroup(id){
            if(id != -1){
                var sid = this.groups.findIndex((g)=>{
                    return g.group_idx==id
                })
                this.$set(this, 'tempStudents', this.groups[sid])
            }// if
            this.$set(this, 'groupTab', id)

        },

        // 
        selectTab(s) {
            this.tab=s;
        },

        sessionTabChange(jj){
            this.$set(this, 'sessionTab', jj)
        },

        sessionTabChange2(jj){
            this.$set(this, 'sessionTab2', jj)
        },

        getLecture(id){
            this.$http.get('/api/lectures/dt/'+id)
            .then(resp=>{
                // console.log(resp.data);
                this.$set(this, 'lecture', resp.data.lecture)
                this.$set(this, 'companies', resp.data.companies)
                this.$set(this, 'groups', resp.data.groups)
                this.$set(this, 'students', resp.data.students)
                this.$set(this, 'kpi', resp.data.kpi)
            }).catch(err=>{
                // console.log(err);
                alert('Error')
            })
        }, // getLecture





        //    
        attendancePercent(sid){
            var stds = this.students
            var all = stds.length

            var temp
            var sum=0
            for(var ii  in  stds){
                temp = JSON.parse(stds[ii].stu_attendance)
                sum+=temp[sid] ? 1 : 0
            }
            var ff = (all/sum) * 1

            return ff.toFixed(1)
        },





        // 
        attendanceCheck(stdId, atdId){
            /*
            stdId :  
            atdId :   
            */

            // var ar = this.students.findIndex((std)=>{
            //     return std.stu_idx == stdId
            // })



            var attd = JSON.parse(this.students[stdId].stu_attendance) //  
            attd[atdId] = attd[atdId]==1 ? 0 : 1


            // 
            this.$http.put('/api/students/attendance', {
                lec_idx : this.lec_idx,
                stu_idx : this.students[stdId].stu_idx,
                attendance : attd
            })
            .then(resp=>{
                this.students[stdId].stu_attendance = JSON.stringify(attd)
            })
            .catch(err=>{
                console.log("");
                console.log(err);
                alert("Error - ")
            })

        }, // 








        //  
        addTeam(){
            alert(1)
        }, // addTeam()




        // 
        removeLecture(){
            if ( !confirm('   . ?') )
                return


            this.$http.delete('/api/lectures/'+this.lec_idx)
            .then(resp=>{
                alert(' .     .')
                this.$ro.push({ path:'/lectures' })
            })
            .catch(err=>{
                alert('Error - r')
            })
        },// removeLecture()



    }// methods
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.segment{
    padding:20px;
}


.lectureInfo a:hover{color:#c1c1c1;}
.lectureInfo a{color:#333; text-decoration: underline; cursor: pointer;}




th{
    background: #F9FAFB;
    padding:0.92857143em 0.78571429em;
}
.borderBottom{
    border-bottom: 1px solid rgba(34, 36, 38, 0.1);
}
.borderTop{
    border-top: 1px solid rgba(34, 36, 38, 0.1);
}


.tempRow{ cursor: pointer; }
.tempRow:hover{ background: #f9f9f9 !important; }




    .lectureMenu .active{
        color: #fff !important;
        background: #3b485f !important;
        border-radius: 0 !important;
    }

    .tab.segment{
        padding:30px 25px !important;
    }




    .list > .item {
        margin:3px;
        padding-top: 10px;
        padding-bottom: 5px;
    }



    h3 .icon {
        color:rgba(0, 0, 0, 0.45) !important;
        font-size: 0.95em !important;
    }



    .moduleLabel{
        padding:0 !important;
        width:57px !important;
    }
    .moduleContent{
        padding:0 !important;
        width:calc(90% - 60px) !important;
    }


    .fixButton{
        position: absolute;
        top: 0;
        right:0;
        z-index: 99;
    }


</style>



